import Component from 'react';

var Carousel = require('react-bootstrap/lib/Carousel');

const MyCarousel = ({images}) => (

    <Carousel>
        {images.map( (img, i) => (
            <Carousel.Item key={i}>
                <img width={900} height={500} alt="900x500" src={`http://localhost:8080${img.url}`}></img>
            </Carousel.Item>
        ))}
    </Carousel>
)

export default MyCarousel;


