import { Component } from 'react';

class Image extends Component {
    constructor(props){
        super(props);
        this.handleClick = this.handleClick.bind(this);
        this.state = {
            open: false,
        }
    }

    render(){
        const containerClass = `image-container ${(this.state.open ? "image-container-open" : "" )}`;
        const backgroundClass = `modal-background${this.state.open ? "-open" : "" }`;
        const imageClass = `image${this.state.open ? "-open" : ""}`;
        return(
            <div className={containerClass} onClick={this.handleClick}>
                <div className={backgroundClass} >
                    <img alt={this.props.alt} className={imageClass} src={this.props.src} />
                </div>
                <img alt={this.props.alt} className="image-placeholder" src={this.props.src} />
            </div>
        )
    }

    handleClick = () => {
        this.setState({open: !this.state.open });
    }
}

export default Image;


