import {Component} from 'react';
import Link from 'next/link';

const showcaseWeek = 0;
const stuvacWeek = 9;

class WeekButton extends Component {
    constructor(props) {
        super(props);
        this.handleClick = this.handleClick.bind(this);
    }

    render() {
        let className = "week-button";
        if(this.props.isActive)
            className += " week-button-active";

        let content;

        switch(this.props.week){
            case showcaseWeek:
                content = "Foreword";
                break;
            case stuvacWeek:
                content = "Stuvac";
                break;
            default:
                content = `Week ${(this.props.week > stuvacWeek ? this.props.week-1 : this.props.week )}`;
        }

        let toRender;

        if(this.props.isShowcase){
            toRender = (
                <Link prefetch href="/showcase">
                    <div id={this.props.week} className={className}>
                        <span className="span">Showcase</span>
                    </div>
                </Link>
            );
        } else {
            toRender = (
                <div id={this.props.week} className={className} onClick={this.handleClick} >
                    <span className="span">{content}</span>
                </div>
            )
        }

        return(
            <span>
                {toRender}
            </span>
        )
    }

    handleClick() {
        console.log(`WeekButton::handleClick() : week = ${this.props.week}`);
        this.props.changeWeekCallback(this.props.week);
    }
}

export default WeekButton;


