import DateFormatterService from '../../services/DateFormatterService';
import Image from './Image';

import MyCarousel from '../processjournal/Carousel';

const Markdown = require('react-markdown');

const showcaseWeek = 0;
const stuvacWeek = 9;

const Post = ({data, showModalCallback}) => {

	const date = new DateFormatterService(new Date(data.Date));
	const updatedDate = new DateFormatterService(new Date(data.updatedAt));

	let prefix;
	switch(data.Week){
		case showcaseWeek:
			prefix = "Foreword";
			break;
		case stuvacWeek:
			prefix = "Stuvac";
			break;
		default:
			prefix = `Week ${(data.Week > stuvacWeek ? data.Week-1 : data.Week )}`;
			break;
	}

	return(
		<div className="post">
			<div className= "container">
				<div className="row post-header">
					<div className="col-xs-8"><h3 className="post-header-title"> {prefix} - {data.Title}</h3></div>
					<div className="col-xs-4 bottom-column"><h5 className="post-header-date">{date.getWeekDay()} the {date.getMonthDay()}
					<br></br>{date.getMonth()}, {date.getYear()}</h5></div>
				</div>
				{data.Carousel && 
					<div className="col-12">
						<MyCarousel images={data.Carousel}> </MyCarousel>
					</div>
				}
				<div className="col-12">
						<Markdown className="markdown" escapeHtml={false} source={data.Content} renderers={ {image: Image} } />
				</div>
				<div className="col-12 update-at">
					<p >Updated At: {date.getWeekDay()} the {date.getMonthDay()} of {date.getMonth()}, {date.getYear()}</p>
				</div>
			</div>
		</div>
	)
}

export default Post;


